import { hot } from 'react-hot-loader';
import './CommonButton.scoped.scss';



function CommonButton({ children, className, variant = 'base', size, onClick }) {
  return (
    <button
      className={`button button-${variant} button-size-${size} ${className ? className : ''}`}
      onClick={onClick}
    >
      <span>{children}</span>
    </button>
  );
}

export default hot(module)(CommonButton);
